import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {ConfigProvider} from "../../providers/config/config";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";


@IonicPage()
@Component({
  selector: 'page-child-menu',
  templateUrl: 'child-menu.html',
})
export class childMenu {

  menuText;
  childDetails=[];
  appVersion;
  currAppVersion;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public config: ConfigProvider,
              public storage : LocalStorageProvider) {


    this.menuText = JSON.parse(this.storage.retrieveFromStorage("menuText"));
    this.childDetails=JSON.parse(this.storage.retrieveFromStorage("childDetails"));

    this.appVersion = this.config.iSmartVersion;

  }

  checkMenuVersionRequirement = function (menuVersion) {
    this.currAppVersion = this.appVersion.replace(/[.]/g, "");
    menuVersion = menuVersion.replace(/[.]/g, "");

    return menuVersion <= this.currAppVersion;
  };

  childPageSelectorAction = function (menuItem) {
    let valueAfterRemHash = (menuItem.menuLink.replace("#", ""));
    let valueAfterReplaceSlash = (valueAfterRemHash.replace("iCellular/", ""));
    // this.navCtrl.setRoot('WelcomePage');
    this.navCtrl.push(valueAfterReplaceSlash);
  };

  getBorderStyle =function (index) {
    if(index%2==0){
      this.borderStyle="1px solid #e7e7e7";
    }
    else{
      this.borderStyle="";
    }
    return this.borderStyle;
  };


  ionViewDidLoad() {
    console.log('ionViewDidLoad ChildMenuPage');
  }

}



